:root {
  --RoseQuartz: #f6c8cc;
  --Ivory: #ebf5f7;
  --Honeysuckle: #c15b78;
  --Teal: #5bb0ba;
}

body {
  font-family: "Raleway", sans-serif;
  background-color: var(--Ivory);
}

.contact {
  font-size: medium;
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  margin: 0.5vw;
}

.contact-images {
  text-align: center;
}

.contact-images img {
  width: 5vw;
  margin: 2vw;
}

.hr-line {
  color: var(--Teal);
  border: 2px solid var(--Teal);
  border-radius: 5px;
  width: 75vw;
}
